.section{
    width:100%;
    align-items: center;
    justify-content: center;
    height: 91vh;
}

.section div{
    margin-top: 20px;
    color: white;
    text-shadow: 1px 1px #614024;
}

.img{
    border-radius: 10px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.img.projects{
    width:100%;
}

.links{
    display: flex;
    justify-content: center;
}

.links:hover{
    color:black;
}